$(".thumbs-wrapper").on('click', '.btn', function() {
    let pressedBtn = $(this)
    let thumbsWrapper = pressedBtn.parent().parent()
    let thumbsList = thumbsWrapper.children().find('i')
    console.log(thumbsWrapper.children().find('i').hasClass('fa-solid'))

    let positiveLikeNumber = parseInt(thumbsWrapper.find('.thumbs-up').text());
    let negativeLikeNumber = parseInt(thumbsWrapper.find('.thumbs-down').text());
    

    if (thumbsList.hasClass('fa-solid')) {
        // Ja possui likes, fazer tratamento de acordo com qual botao é apertado
        let isPressedBtnRegular = pressedBtn.children(0).hasClass('fa-regular')
        if (isPressedBtnRegular) {
            console.log()
            pressedBtn.parent().siblings('.wrapper').find('i').removeClass('fa-solid').addClass('fa-regular')
            pressedBtn.find('i').removeClass('fa-regular').addClass('fa-solid')

        }
    } else {
        // não tem likes ainda, adcionar normal
    }
})

